import React,{ useState,useEffect } from 'react';
import { Link } from 'react-router-dom';
import cn from 'classnames';        //앞에 cn 은 classname 을 줄여서 쓴것 (사용자임의로 쓸수있다)
import './Header.scss';
import { GiHamburgerMenu } from "react-icons/gi";
import { FaUserCircle } from "react-icons/fa";
import styled,{ css } from 'styled-components'  

const HeaderBlock = styled.div`
  
`;

const Header = () => {
    const [ opener , setOpener ] = useState(false); //초기값 false 로 지정을 해놔야지 jsx 에 나타나지 않도록 한다     클래스명을 변수로 써야하기 때문에 classnames 라는 라이브러리가 필요하다  클래스 opener 를 붙인다 두번쨰 setOpenr 는 이벤트를 주기위한 함수 opener 는 클래스 이름도되고 변수의 역할도 해준다
    const openclose = () => {
        setOpener( !opener )              //openclose 함수가 실행된다면 setOpenr(!opener) = useState(true) 가된다 계속해서 클릭시 true 값과 false 값이 변하면서 class 가 바뀜
    }
    //함수이벤트를 만들어서 depth2open  depth2Close  ! 값으로 이벤트 제어를한다
    const [ on , setOn]  = useState(false);
  


    const useScroll =()=>{      //일종의 사용자가만드는 hook 이 된다
        //스크롤이벤트 키값   scy 는 y라는 변하는 객체를 담는 변수가 된다
        const [scy, setScy] = useState( { y : 0 } )
        //스크롤이벤트가 발생할시 기능이 발동되는 함수
        const onScroll = () =>{
            setScy({
                y : window.scrollY  // scy 의 윈도우 스크롤 값을 스크롤이벤트가발생시 y 값을 변경시킨다 최종적으로 스크롤이벤트가 발생하면 전달할값이다
            })
        }

        //처음에 useEffect 함수가 실행이되고
        useEffect(
            () =>{      //componentDidMount() 함수와 비슷하다 랜더함수가 완료되면 실행되는함수
                window.addEventListener("scroll", onScroll)      //윈도우에 스크롤 이벤트가 발행한다면 실행될 기능 함수 사용자 정의 함수
                return () => window.removeEventListener("scroll",onScroll)
                //EventListener가 등록했을때 사용하지않고 오류가 발견될수 있기때문에 이벤트를 사용하지 않을떄는 이벤트를 없애준다
            },[]    //비어있는 [] 을 넣어주면 스크롤 이벤트를 한번만 수행하게 해준다 제이쿼리 .stop() 와 같은 역할을한다
         )
        return scy    //jsx 에 스크롤이벤트가 발생시 window.scrollY = scy 를 리턴해준다 
    }
    //스크롤 이벤트가 발생시 사용할수 있도록 함수를 호출한다 //배열에 키값을 넣어서 호출 결국  y의 키값에서는 scy를 전달받게된다
    const { y } = useScroll();  
    // console.log( y )


    // 사망조건 연산자를 사용해서 쓸수있는데 y키값이 1이상 올라가면 참값이 되고 0이되면 거짓이되도록 한다
    return (    
        <HeaderBlock className="headerwrap" style={{ backgroundColor : y ? '#fff' : "rgba(0,0,0,0.3)" , color: y ? "#ff385c" : "#fff" }}>
            <div className="row">
                <h1 className="logo">
                    <Link className="lnk" to="/">
                        home
                    </Link>
                </h1>
                <nav className="nav">
                    <ul className="depth1"  >
                        <li onMouseEnter={()=>setOn(true)  } onMouseLeave={()=>setOn(false)}  style={{ color : y ? '#000' : '#fff' }}>
                                About Me
                                <ul className={ cn ( "depth2" , { on } ) }>
                                    {/*cn("클래스명",{이벤트가 일어날시 추가될클래스명})*/}
                                    <li><Link className="lnk" to ="/product">
                                        메뉴1
                                        </Link></li>
                                    <li>메뉴2</li>
                                    <li>메뉴3</li>
                                </ul>
                        </li>
                        <li style={{ color : y ? '#000' : '#fff' }}>
                                <Link to="/news">Portfolio</Link>
                        </li>
                        <li style={{ color : y ? '#000' : '#fff' }}>
                                <Link to="/menutab">Skills</Link>
                        </li>
                        <li style={{ color : y ? '#000' : '#fff' }}>
                                <Link to="/menutab">Contact Me</Link>
                        </li>
                    </ul>
                </nav>
                <div className="mobileNav">
                    <button type="button" onClick={ openclose }> 
                        <GiHamburgerMenu className="ham" />
                        <FaUserCircle className="person" />
                    </button>
                    <div className={ cn ( "mobileNavList" , { opener } )}>
                        <ul>
                            <li><Link to="/login">Login</Link></li>
                            <li><Link to="/join">Join</Link></li>
                            <li><Link to="/roadmap">찾아오시는길</Link></li>
                        </ul>
                    </div>
                </div>
            </div>
            <div className='headerBlcok'></div>
        </HeaderBlock>
    );
};

export default Header;