import React, { useState } from 'react';
import cn from 'classnames';
import styled from 'styled-components'
import Slider from "react-slick";
import MenuSlider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import './Homesection.scss';
import  { BsCaretLeft , BsCaretRight } from 'react-icons/bs'

// import imgD from '../images/img5.jpg';

const Article = styled.div`

    .slide1{
        background-image:url('images/guelpark.png');
        background-position:bottom;
    };
    .slide2{
        background-image:url('images/mobile_seoul.jpg');
        background-position:top !important; 
        background-size:cover !important;
    };
    .slide3{
        background-image:url('images/peileTheme.png');
       
    };

    .slick-arrow {
        position:absolute;        ;
        top:50%;
        transform:translateY(-50%);
        background :none;       
        padding:10px;
        z-index:99999;
        color:rgba(0,0,0,1);
        width:8vw;
        height:8vw;
            .nextArrow{
                display:none;
                &.nextSlideHover{
                    display:block;
                }
            }

            .prevArrow{
                display:none;
                &.prevSlideHover{
                    display:block
                }
            }

            &.slick-prev{
                position: absoulte;
                left:100px;
                bottom:50%;
                height:40%;
                font-size:5vw;
                transform: translateY(-50%);
                background:rgba(0,0,0,0);
                color:rgba(0,0,0,0.5);
            }   

            &.slick-next{
                position: absoulte;
                right:100px;
                font-size:5vw;
                bottom:50%;
                height:40%;
                transform: translateY(-50%);
                background:rgba(0,0,0,0);
                color:rgba(0,0,0,0.5);
            }     
    }
    .slick-dots{
        position:absolute;
        bottom:70px;
        left:50%;
        transform:translate(-50%);
        li{
            &+li{
                padding-left:3vw;
            };
            display:inline-block;
            button{
                width:1.5vw;
                height:1.5vw;
                border-radius:50%;
                background:rgba(255,255,255,0.7);
                text-indent:-99999px;
                overflow:hidden;
            }
            &.slick-active{
                button{
                background:rgba(0,0,0);
                }
            }
        }
    }
`;

const AboutMe = styled.div`
    margin: 30px 0;
    height:40vw !important;
    background: url(images/logokimcenter.png) no-repeat center;
    background-color: #001378;
    background-size:contain;
`;



const Homesection = () => {
    const  [prevSlideHover,setprevSlideHover]  = useState(false)
    const [nextSlideHover,setnextSlideHover] = useState(false)

        const settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            autoplay: true,
            prevArrow:
            <div className="prevArrow"  onMouseEnter={()=>setprevSlideHover(true)  } onMouseLeave={()=>setprevSlideHover(false)}><p className={ cn('prevArrow', { prevSlideHover } ) }>
                &lt;
            </p></div>,
            nextArrow:
            <div className="nextArrow" onMouseEnter={()=>setnextSlideHover(true)  } onMouseLeave={()=>setnextSlideHover(false)}><p className={ cn('nextArrow', { nextSlideHover } ) }>
                &gt;
            </p></div>,
        };

        const MuSlider = {
            dots: false,
            infinite: true,
            speed: 500,
            slidesToShow: 3,
            slidesToScroll: 1,
            autoplay: true,
            arrows:false,
            draggable: true,
            prevArrow:   
            <div className="prevArrow"  onMouseEnter={()=>setprevSlideHover(true)  } onMouseLeave={()=>setprevSlideHover(false)}><p className={ cn('prevArrow', { prevSlideHover } ) }>
            &lt;
            </p></div>,
            nextArrow:  
            <div className="nextArrow" onMouseEnter={()=>setnextSlideHover(true)  } onMouseLeave={()=>setnextSlideHover(false)}><p className={ cn('nextArrow', { nextSlideHover } ) }>
            &gt;
            </p></div>,
            centerMode:true,
            centerPadding:'90px'
        };
    
       

    return (
        <div className = "homesecWrap">

            <Article className="article1">
                <Slider {...settings} className='outslide'>
                    <div className="slide slide1">
                        <div className="text row">
                            <span>일러스트레이터</span>
                        </div>    
                    </div>
                    <div className="slide slide2">
                        <div className="text row">
                            <span>모바일 페이지
                            <p><a href="alswoekd.dothome.co.kr">바로가기</a></p>
                            </span>
                        </div>
                    </div>
                    <div className="slide slide3">
                        <div className="text row">
                            <span>반응형 웹 디자인</span>
                           <p>
                                <a href="https://alswo515.github.io/index.html/#" target="_blank">
                                    바로가기
                                </a>
                           </p>
                        </div>
                    </div>
                </Slider>
            </Article>


            <AboutMe className="about_me">
                <div className="my">
                        <img src="images/gojob.jpg" alt=""/>
                     <div className="my_flex">
                            <h3>김민재</h3>
                            <p>
                            1995.05.15
                            </p>
                            <p>010-3020-8592</p>
                            <p>alswo515@naver.com</p>
                     </div>
                </div>
                <div className="skils">
                    <h3>WhatCanYouDo?</h3>
                    <p>
                    HTML5,CSS3,JavaScript,
                    </p>
                    <p>JQuery,React,MediaQuery,</p>
                    <p>AdobePhotoShop,</p>
                    <p>AdobeIllustrator</p>
                </div>
                <div className="introduce">
                <h3>Hellow World!</h3>
                    <p>
                    현재에 충실하는 것 뿐 아니라
                    미래를 계획하는 개발자가 되는
                    것이 목표입니다
                    </p>
                </div>
                <div className="address">
                <h3>ADDRESS</h3>
                    <p>
                    서울 송파구 마천동
                    </p>
                </div>
                <div className="school">
                <h3>EDUCATION</h3>
                    <p>
                    신구대학교 졸업
                    </p>
                </div>
            </AboutMe>

            <article className="article2">
                <div className="row">
                    <div className="lifeStyle">
                            <h2>웹 포트폴리오</h2>
                            <ul>
                                <li><a target="_blank" href="alswoekd.dothome.co.kr"><img src="images/seoulmobile.png" alt=""/></a>
                                <strong>seoulmobile</strong>
                                </li>
                                <li><a target="_blank" href="https://alswo515.github.io/time/"><img src="images/timeMobile.png" alt=""/></a>
                                <strong>timeMobile</strong>
                                </li>
                                <li><a target="_blank" href="https://alswo515.github.io/index.html/"><img src="images/pelieTheme.png" alt=""/></a>
                                <strong>pelie Theme</strong>
                                </li>
                            </ul>
                    </div>
                </div>
            </article>



            <section className="article3">
                    <h3>일러스트, 웹 포트폴리오</h3>
                <div className="row">
                <div class="multi_rolling">
                    <h2>GALLERY</h2>
                    <MenuSlider {...MuSlider} class="multi_visual">
                            <div class="multi_slide">
                                <a href="#none">
                                    <img src="images/1234123.png" alt=""/>
                                </a>
                            </div>
                            <div class="multi_slide">
                                <a href="#none">
                                    <img src="images/aveanges.png" alt=""/></a></div>
                            <div class="multi_slide">
                                <a href="#none">
                                    <img src="images/광일축제-2차.png" alt=""/></a></div>
                            <div class="multi_slide">
                                <a href="#none">
                                    <img src="images/권하지도-받지도-마세요.png" alt=""/></a>
                            </div>
                            <div class="multi_slide">
                                <a href="#none"><img src="images/라바타2-명도.png" alt=""/></a></div>
                            <div class="multi_slide">
                                <a href="#none"><img src="images/방사능-인포-그라피2안--1.png" alt=""/></a>
                            </div>
                            <div class="multi_slide">
                                <a href="#none"><img src="images/벌-느낌수정.png" alt=""/></a></div>
                            <div class="multi_slide">
                                <a href="#none"><img src="images/비만공모전-수정.png" alt=""/></a></div>
                            <div class="multi_slide">
                                <a href="#none"><img src="images/외부광고판.png" alt=""/></a></div>
                            <div class="multi_slide">
                                <a href="#none"><img src="images/우산리뉴얼.png" alt=""/></a></div>
                            </MenuSlider>
                </div>
                </div>
            </section>
        </div>
    );
};

export default Homesection;